import React from 'react';
import { BarChart, Bar,XAxis, YAxis, CartesianGrid, Tooltip, Legend } from "recharts";
import { Card } from '@material-ui/core';

export default class BarChartComponent extends React.Component {
    
    render() {
        return (
            <Card>
                <div style={{fontWeight: 'bold', marginTop:10}}>
                    {this.props.nameKey[0].toUpperCase()+this.props.nameKey.substr(1)} Variations
                </div>
                <BarChart width={300} height={150} data={this.props.data}>
                    <Bar dataKey={this.props.nameKey} fill='#8884d8' />
                    <Bar dataKey={this.props.dataKey} fill='#8884d8' />
                    <CartesianGrid strokeDasharray="3 3"/>
                    <XAxis dataKey={this.props.nameKey}/>
                    <YAxis/>
                    <Tooltip/>
                    <Legend/>
                </BarChart>
            </Card>
        );
    }
}