import React, { Component } from 'react';
import NavBar from './components/NavBar';
import { Card, Button, } from '@material-ui/core';
import MetricsComponent from './components/MetricsComponent';
import BarChartComponent from './components/BarChartComponent';
import LoadTimeComponent from './components/LoadTimeComponent';
import axios from 'axios';
import PieChartComponent from './components/PieChartComponent';
import './App.css';


const styles = {
  dataContent: {
    marginTop: '20px',
    display: 'flex',
    width: '100vw',
    justifyContent :'center'
  },

  flexChild: {
    margin: 'auto'
  },

  pageHeading: {
    marginTop : '100px',
    marginLeft: 'auto',
    marginRight: 'auto',
    padding: '10px 0px',
    display:'flex',
    fontSize: 20,
    fontWeight: '500',
    width: '95%',
    textAlign : 'left'
  }
}

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      data: null,
      curSelected: 1,
      showDrop : false
    }
  }

  UNSAFE_componentWillMount(){
    this.getAnalyticsData();
    //  console.log("webanalytics check", window.location.origin);
  }

  getAnalyticsData(){
    console.log("inside the fucntion for the api call");
    axios.get(window.location.origin +'/analytics/all').then((result)=>{
      console.log("getting all the data for analytics successful",result);
      this.setState({data: result.data});
    }).catch((err)=>{
      console.log("some error in getting the data", err);
    })
  }

  hourData(){
    axios.get(window.location.origin+'/analytics/all/2').then((result)=>{
      console.log("getting all the data for analytics successful",result);
      this.setState({data: result.data,showDrop: false});
    }).catch((err)=>{
      console.log("some error in getting the data", err);
    })
  }

  minuteData(){
    axios.get(window.location.origin+'/analytics/all/1').then((result)=>{
      console.log("getting all the data for analytics successful",result);
      this.setState({data: result.data,showDrop: false});
    }).catch((err)=>{
      console.log("some error in getting the data", err);
    })
  }

  daysData(){
    axios.get(window.location.origin+'/analytics/all/3').then((result)=>{
      console.log("getting all the data for analytics successful",result);
      this.setState({data: result.data,showDrop: false});
    }).catch((err)=>{
      console.log("some error in getting the data", err);
    })
  }

  openDrop(){
    this.setState({
      showDrop: true
    });
  }

  render() {
    return (
      <div className="App">
          <NavBar title="Analytics"/>
        {
          (this.state.data) ? 
          <div>
          <Card style={styles.pageHeading}>
            <span style={{marginLeft:15}}>Dashboard</span>
            <span style={{flexGrow:1}}></span>
            <span style={{marginRight: 15}}>Total Hits: {this.state.data.hits}</span>
          </Card>
          
          
          
          
          <div id="summary-metrics" style={{width: '95%',margin:'0px auto',marginTop: 20}}>
            <MetricsComponent temp= {this.state.data.visits}/>
            <div style={{margin: 10}}>
            <Button color="primary" variant="outlined" style={{margin: 10}} onClick={()=>this.hourData()}>Hour</Button>
            <Button color="primary" variant="outlined" style={{margin: 10}} onClick={()=>this.minuteData()}>Minute</Button>
            <Button color="primary" variant="outlined" style={{margin: 10}} onClick={()=>this.daysData()}>Day</Button>
            </div>
          </div>
          <div style={styles.dataContent}>
            <div style={{margin:'auto', width:'45%'}}>
              <PieChartComponent data={this.state.data.browsers} dataKey="count" nameKey="browsers"/>
            </div>
            <div style={{margin:'auto', width:'45%'}}>
              <PieChartComponent data={this.state.data.os} dataKey="count" nameKey="os"/>
            </div>
          </div>
          <div style={styles.dataContent}>
            <div style={{margin:'auto', width:'45%'}}>
              <BarChartComponent data={this.state.data.languages} hits={this.state.data.hits} dataKey="count" nameKey="languages"/>
            </div>
            <div style={{margin:'auto',width:'45%'}}>
              <LoadTimeComponent data={this.state.data.avg_load_time} isCountry={false}/>
            </div>
          </div>
          <div id="summary-metrics" style={{marginTop: 20,width: '95%',margin:'20px auto'}}>
            <LoadTimeComponent data = {this.state.data.country} isCountry={true}/>
          </div>
          </div> : 
          <div style={{display:'flex', height: '100%'}}>
            <p style={{margin:'auto'}}>No Data Check Again Later</p>
          </div>
        }
      </div>
    );
  }
}

export default App;
