import React from 'react';
import { Card } from '@material-ui/core';

export default class LoadTimeComponent extends React.Component {

    render() {
        return (
            <Card style={{ display: 'block', minHeight: '15vh', overflow: 'auto' }}>
                <div style={{ fontWeight: 'bold', marginTop: 10 }}>
                    {this.props.isCountry ? "Country Variations For Users" : "Load Time Per Page"}
                </div>
                <div style={{ height: '130px', paddingBottom: 20 }}>
                    {
                        this.props.data ?
                            this.props.data.map((items, index) => (
                                !this.props.isCountry ?
                                    <span key={index} style={{ display: 'flex' }}>
                                        <span style={{ marginLeft: 10, marginTop: 5 }}>{items.page}</span>
                                        <span style={{ flexGrow: 1 }}></span>
                                        <span style={{ marginTop: 5, marginRight: 10 }}>{items.avg.toFixed(5)} sec</span>
                                    </span> :
                                    <span key={index} style={{ display: 'flex' }}>
                                        <span style={{ marginLeft: 10, marginTop: 5 }}>{items.country}</span>
                                        <span style={{ flexGrow: 1 }}></span>
                                        <span style={{ marginTop: 5, marginRight: 10 }}>{items.count} users</span>
                                    </span>
                            )) : null
                    }
                </div>
            </Card>
        );
    }
}