import React, { Component } from 'react';
import { PieChart, Pie, Legend, Cell } from 'recharts';

import { Card } from '@material-ui/core';

const COLORS = ['#0088FE', '#00C49F', '#FFBB28', '#FF8042'];


export default class ChartComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {

        }
    }

    render = () => {
        return (
            <Card style={{ textAlign: 'center' }}>
                <div style={{ fontWeight: 'bold', marginTop: 10 }}>
                    {this.props.nameKey[0].toUpperCase() + this.props.nameKey.substr(1)} Variations
                </div>
                <PieChart width={400} height={400}>
                    <Pie data={this.props.data} dataKey={this.props.dataKey} nameKey={this.props.nameKey} cx={200} cy={200} outerRadius={60} fill="#8884d8" label>
                        {
                            this.props.data ? this.props.data.map((entry, index) => <Cell key={index} fill={COLORS[index % COLORS.length]} />) : null
                        }
                    </Pie>
                    <Legend />
                </PieChart>
            </Card>
        );
    }
}

