import React, { Component } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from 'recharts';

export default class ChartComponent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: {
                type: 'Chart Heading'
            },
            
        }
    }

    render = () => {
        return (
            <div style={{textAlign:'center',margin:'auto'}}>
                <LineChart width={700} height={300} data={this.props.chartData}
                    margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
                    <XAxis dataKey={this.props.dataKey} />
                    <YAxis dataKey={this.props.nameKey}/>
                    <CartesianGrid strokeDasharray="3 3" />
                    <Tooltip />
                    <Legend />
                    <Line type="monotone" dataKey={this.props.nameKey} stroke="#8884d8" activeDot={{ r: 8 }} />
                    <Line type="monotone" dataKey={this.props.dataKey} stroke="#82ca9d" />
                </LineChart>
            </div>
        );
    }
}

